import React, { useState, useEffect } from "react";
import Barcode from "react-barcode";
import "./Dashboard.css";

const Inventory = () => {
  const [items, setItems] = useState([]);
  const [search, setSearch] = useState("");
  const [selectedCategory, setSelectedCategory] = useState("");
  const [showAddForm, setShowAddForm] = useState(false);
  const [showUpdateModal, setShowUpdateModal] = useState(false);
  const [currentItem, setCurrentItem] = useState(null);
  const [newItem, setNewItem] = useState({
    name: "",
    sku: "",
    barcode: "", // Added barcode
    quantity: 0,
    threshold: 0,
    category: "Select Category", // Default to "Select Category"
  });

  // Fixed categories
  const fixedCategories = ["Production", "Office", "Car Wash", "Paint/Damage"];

  // Fetch data from the API

  useEffect(() => {
    fetch("http://127.0.0.1:8000/api/inventory/")
      .then((response) => response.json())
      .then((data) => setItems(data))
      .catch((error) => console.error("Error fetching items:", error));
  }, []);

  const handleAddItem = (e) => {
    e.preventDefault();
    if (newItem.category === "Select Category" || newItem.category === "") {
      alert("Please select a valid category.");
      return;
    }
    if (!newItem.barcode) {
      alert("Barcode is required. Please generate or scan a barcode.");
      return;
    }

    fetch("http://127.0.0.1:8000/api/inventory/", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(newItem),
    })
      .then((response) => response.json())
      .then((addedItem) => {
        setItems((prevItems) => [...prevItems, addedItem]);
        setShowAddForm(false);
        setNewItem({
          name: "",
          sku: "",
          barcode: "",
          quantity: 0,
          threshold: 0,
          category: "Select Category",
        });
      })
      .catch((error) => console.error("Error adding item:", error));
  };
  // Handle opening the update modal
  const handleOpenUpdateModal = (item) => {
    setCurrentItem(item);
    setShowUpdateModal(true);
  };

  // Handle updating an item
  const handleUpdateItem = (e) => {
    e.preventDefault();
    if (!currentItem.category || currentItem.category === "Select Category") {
      alert("Please select a valid category.");
      return;
    }
    if (!newItem.barcode) {
      alert("Barcode is required. Please generate or scan a barcode.");
      return;
    }

    fetch(`http://127.0.0.1:8000/api/inventory/${currentItem.id}/`, {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(currentItem),
    })
      .then((response) => response.json())
      .then((updatedData) => {
        setItems((prevItems) =>
          prevItems.map((item) =>
            item.id === updatedData.id ? updatedData : item
          )
        );
        setShowUpdateModal(false);
      })
      .catch((error) => console.error("Error updating item:", error));
  };
  // Handle deleting an item
  const handleDeleteItem = (id) => {
    fetch(`http://127.0.0.1:8000/api/inventory/${id}/`, { method: "DELETE" })
      .then(() => {
        setItems((prevItems) => prevItems.filter((item) => item.id !== id));
      })
      .catch((error) => console.error("Error deleting item:", error));
  };

  const generateBarcode = () => {
    setNewItem({ ...newItem, barcode: `BAR${Date.now()}` });
  };

  const scanBarcode = () => {
    alert("Scanning functionality is not implemented yet.");
    // Implement scanning functionality here.
  };

  // Handle search and category filtering
  const filteredItems = items.filter(
    (item) =>
      item.name?.toLowerCase().includes(search.toLowerCase()) &&
      (selectedCategory === "" || item.category === selectedCategory)
  );

  return (
    <div className="dashboard-section">
      <h2 className="dashboard-title">Inventory Management</h2>
      <div className="dashboard-filters">
        <input
          type="text"
          placeholder="Search items..."
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="search-input"
        />
        <select
          value={selectedCategory}
          onChange={(e) => setSelectedCategory(e.target.value)}
          className="category-select"
        >
          <option value="">All Categories</option>
          {fixedCategories.map((category) => (
            <option key={category} value={category}>
              {category}
            </option>
          ))}
        </select>
      </div>
      <button
        className="add-item-button"
        onClick={() => setShowAddForm(!showAddForm)}
      >
        {showAddForm ? "Cancel" : "Add Item"}
      </button>
      {showAddForm && (
        <form className="add-item-form" onSubmit={handleAddItem}>
          <input
            type="text"
            placeholder="Name"
            value={newItem.name}
            onChange={(e) => setNewItem({ ...newItem, name: e.target.value })}
            required
          />
          <input
            type="text"
            placeholder="SKU"
            value={newItem.sku}
            onChange={(e) => setNewItem({ ...newItem, sku: e.target.value })}
            required
          />
          <div className="barcode-options">
            <button type="button" onClick={generateBarcode}>
              Generate Barcode
            </button>
            <button type="button" onClick={scanBarcode}>
              Scan Barcode
            </button>
          </div>
          {newItem.barcode && <Barcode value={newItem.barcode} />}
          <input
            type="number"
            placeholder="Quantity"
            value={newItem.quantity}
            onChange={(e) =>
              setNewItem({ ...newItem, quantity: parseInt(e.target.value) })
            }
            required
          />
          <input
            type="number"
            placeholder="Threshold"
            value={newItem.threshold}
            onChange={(e) =>
              setNewItem({ ...newItem, threshold: parseInt(e.target.value) })
            }
            required
          />
          <select
            value={newItem.category}
            onChange={(e) =>
              setNewItem({ ...newItem, category: e.target.value })
            }
            required
          >
            <option value="Select Category">Select Category</option>
            {fixedCategories.map((category) => (
              <option key={category} value={category}>
                {category}
              </option>
            ))}
          </select>
          <button type="submit" className="submit-button">
            Add Item
          </button>
        </form>
      )}
      <table className="inventory-table">
        <thead>
          <tr>
            <th>Name</th>
            <th>SKU</th>
            <th>Barcode</th>
            <th>Quantity</th>
            <th>Category</th>
            <th>Threshold</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {filteredItems.map((item) => (
            <tr key={item.id}>
              <td>{item.name || "Unnamed Item"}</td>
              <td>{item.sku || "No SKU"}</td>
              <td>
                {item.barcode ? <Barcode value={item.barcode} /> : "No Barcode"}
              </td>
              <td className={item.quantity < item.threshold ? "low-stock" : ""}>
                {item.quantity}
              </td>
              <td>{item.category || "Uncategorized"}</td>
              <td>{item.threshold}</td>
              <td>
                <button
                  className="update-button"
                  onClick={() => handleOpenUpdateModal(item)}
                >
                  Update
                </button>
                <button
                  className="delete-button"
                  onClick={() => handleDeleteItem(item.id)}
                >
                  Delete
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {showUpdateModal && currentItem && (
        <div className="modal-overlay">
          <div className="modal-content">
            <h3>Update Item</h3>
            <form onSubmit={handleUpdateItem}>
              <input
                type="text"
                placeholder="Name"
                value={currentItem.name}
                onChange={(e) =>
                  setCurrentItem({ ...currentItem, name: e.target.value })
                }
                required
              />
              <input
                type="text"
                placeholder="SKU"
                value={currentItem.sku}
                onChange={(e) =>
                  setCurrentItem({ ...currentItem, sku: e.target.value })
                }
                required
              />
              <input
                type="text"
                placeholder="Barcode"
                value={currentItem.barcode}
                onChange={(e) =>
                  setCurrentItem({ ...currentItem, barcode: e.target.value })
                }
                required
              />
              <input
                type="number"
                placeholder="Quantity"
                value={currentItem.quantity}
                onChange={(e) =>
                  setCurrentItem({
                    ...currentItem,
                    quantity: parseInt(e.target.value),
                  })
                }
                required
              />
              <input
                type="number"
                placeholder="Threshold"
                value={currentItem.threshold}
                onChange={(e) =>
                  setCurrentItem({
                    ...currentItem,
                    threshold: parseInt(e.target.value),
                  })
                }
                required
              />
              <select
                value={currentItem.category || ""}
                onChange={(e) =>
                  setCurrentItem({ ...currentItem, category: e.target.value })
                }
                required
              >
                <option value="Select Category">Select Category</option>
                {fixedCategories.map((category) => (
                  <option key={category} value={category}>
                    {category}
                  </option>
                ))}
              </select>
              <div className="modal-actions">
                <button type="submit" className="submit-button">
                  Update
                </button>
                <button
                  type="button"
                  className="cancel-button"
                  onClick={() => setShowUpdateModal(false)}
                >
                  Cancel
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default Inventory;
