import React, { useState, useEffect } from "react";
import "./Dashboard.css";
import UploadModal from "./UploadModal";

const Cars = () => {
  const [cars, setCars] = useState([]);
  const [categories, setCategories] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedCategory, setSelectedCategory] = useState("");

  // Fetch cars data
  useEffect(() => {
    fetch("/api/cars/")
      .then((response) => response.json())
      .then((data) => setCars(data));

    fetch("/api/categories/")
      .then((response) => response.json())
      .then((data) => setCategories(data));
  }, []);

  // Filter cars by category
  const handleFilter = (category) => {
    fetch(`/api/cars/?category=${category}`)
      .then((response) => response.json())
      .then((data) => setCars(data));
  };

  // Handle category change
  const handleCategoryChange = (event) => {
    const category = event.target.value;
    setSelectedCategory(category);
    handleFilter(category);
  };

  return (
    <div className="dashboard-section">
      <h2 className="dashboard-title">Cars Management</h2>
      <div className="filters">
        <label htmlFor="category-filter">Filter by Category:</label>
        <select
          id="category-filter"
          value={selectedCategory}
          onChange={handleCategoryChange}
          className="category-select"
        >
          <option value="">All Categories</option>
          {categories.map((category) => (
            <option key={category.id} value={category.name}>
              {category.name}
            </option>
          ))}
        </select>
      </div>
      <button className="upload-button" onClick={() => setIsModalOpen(true)}>
        Upload Excel
      </button>
      <table className="inventory-table">
        <thead>
          <tr>
            <th>VIN</th>
            <th>Model</th>
            <th>Scheduled Date</th>
            <th>Status</th>
          </tr>
        </thead>
        <tbody>
          {cars.map((car) => (
            <tr key={car.id}>
              <td>{car.vin}</td>
              <td>{car.model}</td>
              <td>{car.scheduled_date}</td>
              <td>{car.status.replace("_", " ")}</td>
            </tr>
          ))}
        </tbody>
      </table>
      {isModalOpen && <UploadModal setIsModalOpen={setIsModalOpen} setCars={setCars} />}
    </div>
  );
};

export default Cars;
