import React, { useState } from "react";
import * as XLSX from "xlsx";
import "./Dashboard.css";

const UploadModal = ({ setIsModalOpen, setCars }) => {
  const [previewData, setPreviewData] = useState([]);

  const handleFileUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      const reader = new FileReader();

      reader.onload = (event) => {
        const binaryStr = event.target.result;
        const workbook = XLSX.read(binaryStr, { type: "binary" });
        const sheetName = workbook.SheetNames[0];
        const sheet = workbook.Sheets[sheetName];
        const jsonData = XLSX.utils.sheet_to_json(sheet);

        // Extract key fields
        const extractedData = jsonData.map((row) => ({
          vin: row["VIN"] || "N/A",
          model: row["Model"] || "Unknown",
          adaptation: row["Adaptation description"] || "None",
          scheduled_date: row["Estimated Date"] || "N/A",
          location: row["Location"] || "Unknown",
          part: row["Part"] || "Miscellaneous",
          sku: row["SKU"] || "N/A",
          stock: row["Stock"] || 0,
          threshold: row["Threshold"] || 5,
          needed_quantity: row["Needed Quantity"] || 1,
        }));

        setPreviewData(extractedData);
      };

      reader.readAsBinaryString(file);
    }
  };

  const handleConfirm = async () => {
    try {
      // Step 1: Create categories
      const categoryPromises = previewData.map((row) => {
        const categoryName = row.part || "Miscellaneous"; // Ensure part data is valid
        console.log("Creating category:", categoryName); // Debugging log
        return fetch("http://127.0.0.1:8000/api/categories/", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ name: categoryName }),
        }).then((response) => {
          if (!response.ok) {
            throw new Error(`Failed to create category: ${categoryName}`);
          }
          return response.json();
        });
      });

      const categories = await Promise.all(categoryPromises);

      // Step 2: Create inventory items
      await Promise.all(
        previewData.map((row) => {
          const category = categories.find((cat) => cat.name === row.part);
          return fetch("http://127.0.0.1:8000/api/inventory/", {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({
              name: row.part,
              sku: row.sku,
              category: category.id,
              quantity: row.stock,
              threshold: row.threshold,
            }),
          });
        })
      );

      // Step 3: Link cars to inventory
      await Promise.all(
        previewData.map((row) =>
          fetch("http://127.0.0.1:8000/api/cars/", {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({
              vin: row.vin,
              model: row.model,
              scheduled_date: row.scheduled_date,
              status: "upcoming",
              required_parts: [
                {
                  inventory_item: row.part, // Part name
                  quantity_needed: row.needed_quantity,
                },
              ],
            }),
          })
        )
      );

      alert("Cars and parts added successfully!");
      setIsModalOpen(false);
    } catch (error) {
      console.error("Error during upload:", error);
      alert("Failed to upload data. Check console for details.");
    }
  };

  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <h3>Preview Cars</h3>
        <input
          type="file"
          accept=".xlsx, .xls"
          onChange={handleFileUpload}
          className="file-input"
        />
        <table className="inventory-table">
          <thead>
            <tr>
              <th>VIN</th>
              <th>Model</th>
              <th>Adaptation</th>
              <th>Scheduled Date</th>
              <th>Location</th>
            </tr>
          </thead>
          <tbody>
            {previewData.map((car, index) => (
              <tr key={index}>
                <td>{car.vin}</td>
                <td>{car.model}</td>
                <td>{car.adaptation}</td>
                <td>{car.scheduled_date}</td>
                <td>{car.location}</td>
              </tr>
            ))}
          </tbody>
        </table>
        <button onClick={handleConfirm} className="confirm-button">
          Confirm and Add
        </button>
        <button onClick={() => setIsModalOpen(false)} className="cancel-button">
          Cancel
        </button>
      </div>
    </div>
  );
};

export default UploadModal;
